	module.exports = {"data":{"markdownRemark":{"html":"<p>\"Captain McGiblets Book of Nautical Secrets\", a CRM and project tracking tool, was one of the first projects I did using React. This was another project with my friends at <a href=\"/ics\">ICS</a>. Built on <a class=\"effect-3\" href=\"https://www.meteor.com/\" target=\"_blank\">MeteorJS</a> and <a class=\"effect-3\" href=\"https://facebook.github.io/react/\" target=\"_blank\">React</a>, this web app is blazing fast and fun to use!</p>\n\n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 57.8125%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"App Dashboard\" title=\"\" src=\"/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-ca545.jpg\" srcset=\"/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-aa1b7.jpg 148w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-b937a.jpg 295w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-ca545.jpg 590w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-75639.jpg 885w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-8bd51.jpg 1180w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-4d2dd.jpg 1280w\" sizes=\"(max-width: 590px) 100vw, 590px\">\n    </span>\n  </span>\n  \n<p>User roles were a big priority for the end product. Different levels of users can view different entities entered into the CRM and monitor updates associated with their projects, posted by lower-level users. These updates can even include different attachments of any type!</p>","frontmatter":{"path":"/captain-mcgiblets-book-nautical-secrets","title":"Captain McGiblet's Book of Nautical Secrets - Meteor App","featuredImage":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.7297297297297298,"src":"/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-f2ac0.jpg","srcSet":"/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-567e3.jpg 158w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-c3206.jpg 315w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-f2ac0.jpg 630w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-0c4a5.jpg 945w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-211f3.jpg 1260w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-1a402.jpg 1280w","sizes":"(max-width: 630px) 100vw, 630px"}}}}}},"pathContext":{}}

